import React from 'react';
import {Flex, Typography} from "antd";

const {Text, Paragraph} = Typography;
function RemoveDataPage() {
    return (
        <Flex vertical align="center" className="box">
            <Text style={{fontSize: "2rem"}}>dost o vmaz uivatelskch dat z aplikace Moje Gar</Text>
            <Paragraph>
                Pokud mte zjem o smazn uivatelskho tu, postupujte podle nsledujcch instrukc:
            </Paragraph>
            <ol>
                <li>Smate vechna vozidla v aplikaci.</li>
                <li>Napite na mail <Text strong={true}>patriki01@centrum.cz</Text> dost o smazn tu.</li>
            </ol>
        </Flex>
    );
}

export default RemoveDataPage;